<template>
  <v-container fluid class="homeView px-sm-10">
    <v-snackbar top min-width="50%" color="success" v-model="dataExp.snackbarNewExp" :timeout="5000">
      {{ this.dataExp.msgSuccess }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="dataExp.snackbarNewExp = false">
          Fechar
        </v-btn>
      </template>
    </v-snackbar>

    <v-row class="">
      <v-dialog v-model="dialogEdit" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="text-h5">Editar Despesa</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field :prepend-inner-icon="'mdi-cash-multiple'" v-model="editedItem.value" v-money="money" label="Informe o Valor *"  hint="informe o valor desejado"></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-text-field :prepend-inner-icon="'mdi-file'" v-model="editedItem.desc" label="Descrio" hint="informe a descrio desejada"></v-text-field>
                </v-col>

                <v-col cols="12">
                  <v-menu ref="menu2" v-model="menu2" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field v-model="editedItem.dateFormatted" readonly :prepend-inner-icon="'mdi-calendar'" label="Informe a Data *" 
                      hint="informe a data desejada" v-bind="attrs" @blur="editedItem.date = parseDate(editedItem.dateFormatted)" v-on="on" ></v-text-field>
                    </template>
                    
                    <v-date-picker v-model="editedItem.date" no-title @input="menu2 = false" locale="pt"></v-date-picker>
                  </v-menu>
                </v-col>
               
                <v-col cols="12">
                  <v-select :prepend-inner-icon="'mdi-label'" v-model="editedItem.idCategory" 
                    :items="[this.allFormsPagt[2].categories[0].title, this.allFormsPagt[2].categories[1].title, this.allFormsPagt[2].categories[2].title, 
                    this.allFormsPagt[2].categories[3].title, this.allFormsPagt[2].categories[4].title, this.allFormsPagt[2].categories[5].title,
                    this.allFormsPagt[2].categories[6].title, this.allFormsPagt[2].categories[7].title, this.allFormsPagt[2].categories[8].title]" label="Categoria *" required>
                  </v-select>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogEdit = false">Cancelar</v-btn>
            <v-btn color="blue darken-1" text @click="saveEdit()">Salvar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialogDelete" max-width="600px">
        <v-card>
          <v-card-title class="text-h5">Tem certeza de que deseja excluir esta despesa?</v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialogDelete = false">Cancelar</v-btn>
            <v-btn color="blue darken-1" text @click="deleteItemConfirm()">OK</v-btn>
            <v-spacer></v-spacer>
          </v-card-actions>
        </v-card>
      </v-dialog>
    
      <v-col class="col" :cols="12" :md="8">
        <v-card>
          <v-card-title>
            Despesas
            <v-spacer></v-spacer>
            <v-text-field v-model="search2" append-icon="mdi-magnify" label="Pesquisar" single-line hide-details></v-text-field>
          </v-card-title>
          <v-data-table :no-data-text="'No h dados'" :no-results-text="'Nenhum resultado encontrado'" 
          :header-props="{'sortByText': 'Ordenar por'}" :footer-props="{'items-per-page-text':'Itens por pgina', 
          pageText: '{0}-{1} de {2}', 'items-per-page-all-text':'Todos'}" :headers="headers2" :items="desserts2" 
          :search="search2" :custom-filter="filterData">

            <template v-slot:[`item.desc`]="{ item }">
              <v-tooltip :color="'rgb(0, 0, 0)'" :max-width="220" bottom>
                <template v-slot:activator="{ on, attrs }">
                  <span v-bind="attrs" v-on="on" style="">{{ item.desc }}</span>
                </template> 
                <span>{{ item.desc }}</span>
              </v-tooltip>
            </template>

            <template v-slot:[`item.value`]="{ item }">
              <span>{{ item.value | toBrl }}</span>
            </template>

            <template v-slot:[`item.date`]="{ item }">
              <span>{{ new Date(item.date.replace("-",',')).toLocaleString() | toDate }}</span>
            </template>

            <template v-slot:[`item.action`]="{ item }"> 
              <v-icon small class="mr-2" @click="editItem(item)">
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(item)">
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
      
      <v-col class="col" :cols="12" :md="4" :lg="3">
        <div>
         <v-btn class="mb-2" rounded  color="success" @click="dialog = true">
            Nova Despesa
          </v-btn>
        </div>
        
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <div class="elements" v-bind="attrs" v-on="on">
              <a :href="`#`">
                <div class="left">
                  <span>Despesas</span>
                  <br>
                  
                  <span class="value">R$ {{ sumValues() }}</span>
                </div>
                
                <div class="right">
                  <span>
                    <v-icon class="iGreen">mdi-arrow-up</v-icon>
                  </span>
                </div>
              </a>
            </div>
          </template>
          <span>Valor total de suas despesas</span>
        </v-tooltip>

        <v-row>
          <v-col :cols="12">
            <v-menu style="z-index: 13" ref="menuFilterInit" v-model="menuFilterInit" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="auto">
              <template v-slot:activator="{ on, attrs }">
                <v-text-field v-model="dateFilterFormattedInit" readonly :prepend-inner-icon="'mdi-calendar'" label="Perodo Inicial" 
                hint="informe a data desejada" v-bind="attrs" @blur="dateFilterInit = parseDate(dateFilterFormattedInit)" v-on="on" ></v-text-field>
              </template>
              
              <v-date-picker v-model="dateFilterInit" no-title @input="menuFilterInit = false" locale="pt"></v-date-picker>
            </v-menu>
          </v-col>
          <v-col :cols="12">
            <v-menu style="z-index: 13" ref="menuFilterFinal" v-model="menuFilterFinal" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="auto">
              <template v-slot:activator="{ on, attrs }">
                <v-text-field v-model="dateFilterFormattedFinal" readonly :prepend-inner-icon="'mdi-calendar'" label="Perodo Final" 
                hint="informe a data desejada" v-bind="attrs" @blur="dateFilterFinal = parseDate(dateFilterFormattedFinal)" v-on="on" ></v-text-field>
              </template>
              
              <v-date-picker v-model="dateFilterFinal" no-title @input="menuFilterFinal = false" locale="pt"></v-date-picker>
            </v-menu>
          </v-col>
        </v-row>
        
        <v-btn class="ma-1" color="success" @click="filterByDate()">
          Filtrar
        </v-btn>
        
        <v-btn class="ma-1" color="primary" @click="clearDateFilter()">
          Limpar Filtro
        </v-btn>
      </v-col>
    </v-row>

    <v-row class="reports">
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="text-h5">Nova Despesa</span>
          </v-card-title>

          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field :prepend-inner-icon="'mdi-cash-multiple'" v-model="dataExp.newExpValue" v-money="money" label="Informe o Valor *" hint="informe o valor desejado" required/> 
                </v-col>

                <v-col cols="12">
                  <v-text-field :prepend-inner-icon="'mdi-file'" v-model="dataExp.newExpDesc" label="Descrio" type="text" hint="informe a descrio desejada" required/>
                </v-col>

                <v-col cols="12">
                  <v-menu ref="menu1" v-model="menu1" :close-on-content-click="false" transition="scale-transition" offset-y max-width="290px" min-width="auto">
                    <template v-slot:activator="{ on, attrs }">
                      <v-text-field v-model="dateFormatted" readonly :prepend-inner-icon="'mdi-calendar'" label="Informe a Data *" hint="informe a data desejada" v-bind="attrs" @blur="date = parseDate(dateFormatted)" v-on="on" ></v-text-field>
                    </template>
                    
                    <v-date-picker v-model="date" no-title @input="menu1 = false" locale="pt"></v-date-picker>
                  </v-menu>
                </v-col>

                <v-col cols="12">
                  <v-select :prepend-inner-icon="'mdi-label'" v-model="dataExp.newExpSelect" 
                  :items="[this.allFormsPagt[2].categories[0].title, this.allFormsPagt[2].categories[1].title, this.allFormsPagt[2].categories[2].title, 
                    this.allFormsPagt[2].categories[3].title, this.allFormsPagt[2].categories[4].title, this.allFormsPagt[2].categories[5].title,
                    this.allFormsPagt[2].categories[6].title, this.allFormsPagt[2].categories[7].title, this.allFormsPagt[2].categories[8].title]" label="Categoria *" required>
                  </v-select>
                </v-col>
              </v-row>
            </v-container>
            <small>* indica campo obrigatrio</small>
            <v-spacer></v-spacer>
            <v-btn class="ma-1" color="error" @click="dialog = false">Cancelar</v-btn>
            <v-btn class="ma-1" color="primary" @click="newExp()">Salvar</v-btn>
            <v-btn class="d-none d-sm-inline-flex ma-1"  color="success darken-1" @click="newExp(true)">Salvar e continuar cadastrando</v-btn>
          </v-card-text>
        </v-card>
      </v-dialog>

      <v-dialog v-model="this.dataExp.error" persistent transition="dialog-bottom-transition" max-width="600" style="z-index: 10000;">
        <v-card>
          <v-toolbar color="error" dark>Ocorreu um erro</v-toolbar>
          <v-card-text>
            <div class="text-h4 pa-12">{{ dataExp.msgError }}</div>
          </v-card-text>
          <v-card-actions class="justify-end">
            <v-btn color="error" block @click="dataExp.error = false">Fechar</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
 
      <v-col class="dash" :cols="12" :md="6" :lg="4">
        <h4>Despesas por Categoria</h4>
        
        <div id="first">
          <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data.length == 0 && dataExp.message == ''">
            <v-icon>mdi-chart-donut</v-icon>
            <br>
            <h5>Voc ainda no possui despesas.</h5>
          </div>

          <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data.length == 0 && dataExp.message != ''">
            <v-icon>mdi-chart-donut</v-icon>
            <br>
            <h5>{{ dataExp.message }}</h5>
          </div>
          
          <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data.length > 0 && dataExp.message != ''">
            <v-icon>mdi-chart-donut</v-icon>
            <br>
            <h5>{{ dataExp.message }}</h5>
          </div>

          <div class="divDash" v-if="allFormsPagt[2].data != null && allFormsPagt[2].data.length > 0 && dataExp.message == ''">
            <apexchart class="" height="350" id="apexDonutExp" type="donut" :options="dataExp.optionsDonut" :series="dataExp.seriesDonut"></apexchart>
          </div>
        </div>
      </v-col>
      
      <v-col class="dash" :cols="12" :md="6" :lg="5">
        <h4>Despesas por Ano</h4>
        
        <div id="first">
          <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data.length == 0">
            <v-icon>mdi-chart-donut</v-icon>
            <br>
            <h5>
              Voc ainda no possui despesas.
            </h5>
          </div>

          <div class="divDash" v-else>
            <v-select style="z-index: 12;" v-model="yearSelected" :items="arrayYears" menu-props="auto" label="Select" hide-details :prepend-inner-icon="'mdi-calendar-range'" single-line/>
            <apexchart type="line" height="350" :options="dataExp.optionsLine" :series="dataExp.seriesLine"></apexchart>
          </div>
        </div>
      </v-col>

      <v-col class="dash" :cols="12" :lg="3">
        <h4>Dados Gerais</h4>
        <v-row class="mt-0">
          <v-col class="pt-0" :cols="12" :md="6" :lg="12">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <div class="elements" v-bind="attrs" v-on="on">
                  <span>
                    <div class="left">
                      <span style="font-weight: bold;">Quantidade de Despesas</span>
                      <br>
                      <span v-if="allFormsPagt[2].data != null">{{ allFormsPagt[2].data.length }}</span>
                      <span v-if="allFormsPagt[2].data == null">0</span>
                    </div>
                    
                    <div class="right">
                      <span>
                        <v-icon class="iGreen">mdi-arrow-up</v-icon>
                      </span>
                    </div>
                  </span>
                </div>
              </template>
              <span>Quantidade de Despesas Informadas</span>
            </v-tooltip>
          </v-col>

          <v-col class="pt-0" :cols="12" :md="6" :lg="12">
            <div id="arrayMov">
              <span>
                <h3>
                  ltimas movimentaes criadas
                </h3>

                <div style="text-align: center" v-if="allFormsPagt[2].data != null && allFormsPagt[2].data.length == 0">
                  <v-icon>mdi-chart-donut</v-icon>
                  <br>
                  <h5>
                    Voc ainda no possui despesas.
                  </h5>
                </div>

                <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data[allFormsPagt[2].data.length -1]" class="data">
                  <div>
                    <div class="divLeft">
                      <v-tooltip :color="'rgb(0, 0, 0)'" :max-width="220" bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">{{ allFormsPagt[2].data[allFormsPagt[2].data.length -1].desc }}</span> <br>
                        </template>
                        <span>{{ allFormsPagt[2].data[allFormsPagt[2].data.length -1].desc }}</span>
                      </v-tooltip>
                      <span>{{ formatDate(allFormsPagt[2].data[allFormsPagt[2].data.length -1].date) }}</span>
                    </div>

                    <div class="divRight">
                      <span>
                        R$ {{ allFormsPagt[2].data[allFormsPagt[2].data.length -1].value | toBrl }}
                      </span>
                    </div>
                  </div>
                </div>
                
                <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data[allFormsPagt[2].data.length -2]" class="data">
                  <div>
                    <div class="divLeft">
                      <v-tooltip :color="'rgb(0, 0, 0)'" :max-width="220" bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">{{ allFormsPagt[2].data[allFormsPagt[2].data.length -2].desc }}</span> <br>
                        </template>
                        <span>{{ allFormsPagt[2].data[allFormsPagt[2].data.length -2].desc }}</span>
                      </v-tooltip>
                      <span>{{ formatDate(allFormsPagt[2].data[allFormsPagt[2].data.length -2].date) }}</span>
                    </div>

                    <div class="divRight">
                      <span>
                        R$ {{ allFormsPagt[2].data[allFormsPagt[2].data.length -2].value | toBrl }}
                      </span>
                    </div>
                  </div>
                </div>
                
                <div v-if="allFormsPagt[2].data != null && allFormsPagt[2].data[allFormsPagt[2].data.length -3]" class="data">
                  <div>
                    <div class="divLeft">
                      <v-tooltip :color="'rgb(0, 0, 0)'" :max-width="220" bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <span v-bind="attrs" v-on="on">{{ allFormsPagt[2].data[allFormsPagt[2].data.length -3].desc }}</span> <br>
                        </template>
                        <span>{{ allFormsPagt[2].data[allFormsPagt[2].data.length -3].desc }}</span>
                      </v-tooltip>
                      <span>{{ formatDate(allFormsPagt[2].data[allFormsPagt[2].data.length -3].date) }}</span>
                    </div>

                    <div class="divRight">
                      <span>
                        R$ {{ allFormsPagt[2].data[allFormsPagt[2].data.length -3].value | toBrl }}
                      </span>
                    </div>
                  </div>
                </div>
              </span>
            </div>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Vue from 'vue'
import scrypt from "../../assets/js/scryptExp.js"
import VueApexCharts from 'vue-apexcharts'

import "../../assets/style/style.css"
import money from  'vuejs-money'
Vue.use(money)

Vue.use(VueApexCharts)
Vue.component('apexchart', VueApexCharts)
export default {
  data(){
    return {
      width: 0,
      widthDonut: 0,
      yearSelected: '',
      arrayYears: [],
      picker: 2022,
      dateFilterInit: '',
      dateFilterFormattedInit: '',
      menuFilterInit: false,
      dateFilterFinal: '',
      dateFilterFormattedFinal: '',
      menuFilterFinal: false,
      date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      dateFormatted: this.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
      menu1: false,
      menu2: false,
      search2: '',
      headers2: [
        { text: 'Descrio', align: 'start', value: 'desc'},
        { text: 'Categoria', value: 'idCategory'},
        { text: 'Valor', value: 'value' , searchable: false},
        { text: 'Data', value: 'date' },
        { text: 'Aes', value: 'action', sortable: false }
      ],
      desserts2: [],
      money: {
        decimal: ',',
        thousands: '.',
        prefix: 'R$ ',
        precision: 2,
        masked: false
      },
      dialog: false,
      dialogEdit: false,
      dialogDelete: false,
      allFormsPagt:[],
      editedItem: {
        desc: '',
        idCategory: '',
        value: '',
        date: '',
        dateEdit: '',
      },
      dataExp: {
        indexs:[],
        totalExpHouse: 0,
        totalExpInvestEduc: 0,
        totalExpInvestElet: 0,
        totalExpLaz: 0,
        totalExpOut: 0,
        totalExpRest: 0,
        totalExpSau: 0,
        totalExpServ: 0,
        totalExpSup: 0,
        newExpValue: '0',
        newExpSelect: '',
        newExpDesc: '',
        message: '',
        error: false,
        msgError: '',
        msgSuccess: '',
        snackbarNewExp: false,
        seriesDonut: [],
        optionsDonut: {
          chart: {
            width: '100%',
          },
          colors: 
            ['#2E93fA', '#66DA26', '#546E7A', '#986CDF', '#FF9800', '#29D9D5', '#FFC107', '#FF0000', '#000000']
          ,
          tooltip: {
            enabled: true,
             y: {
              formatter: function (val) {
                return 'R$ ' + (Math.round(val * 100) / 100).toFixed(2).replace(".",",");
              }
            }
          },
          show: true,
          showForZeroSeries: false,
          formatter: function (val) {
            return val + "%"
          },
          legend:{
            position: 'bottom'
          },
          labels: ['Casa', 'Educao', 'Eletrnicos', 'Lazer', 'Outros', 'Restaurante', 'Sade', 'Servios', 'Supermercado'],
        },
        optionsLine: {
          tooltip: {
            enabled: true,
             y: {
              formatter: function (val) {
                return 'R$ ' + (Math.round(val * 100) / 100).toFixed(2).replace(".",",");
              },
              title: {
                formatter : function(){
                  return 'Total:'
                } 
              }
            }
          },
          chart: {
            width: '100%',
            defaultLocale: 'pt-br',
            locales: [{
              name: 'pt-br',
              options: {
                toolbar: {
                  selectionZoom: 'Selecionar Zoom',
                  zoomIn: 'Mais Zoom',
                  zoomOut: 'Reduzir Zoom',
                  pan: 'Panormico',
                  reset: 'Resetar Zoom',
                }
              }
            }],
            toolbar: {
              export: {
                csv: {
                  filename: 'Despesas por ano',
                  columnDelimiter: ';',
                  headerCategory: 'Ms',
                },
                svg:{
                  filename: 'Despesas por ano',
                },
                png:{
                  filename: 'Despesas por ano',
                }
              }
            },
          },
          markers: {
            size: [4,7],
          },
          xaxis: {
            categories: ['Janeiro', 'Fevereiro', 'Maro', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'],
          }
        },
        seriesLine: [{
          name: 'Total',
          data: []
        }],
        
      },
    }
  },
  created(){
    this.allFormsPagt = scrypt.allFormsPagt;

    if(window) {
      this.allFormsPagt[2].data = JSON.parse(localStorage.getItem('dataExp'))

      this.createArrayData();
      if(this.allFormsPagt[2].data != null){
        this.configData();
        this.configDataLine(this.yearSelected);
      } else{
        this.allFormsPagt[2].data = [];
      }
  
      this.dataExp.seriesDonut = [this.dataExp.totalExpHouse, this.dataExp.totalExpInvestEduc, this.dataExp.totalExpInvestElet, this.dataExp.totalExpLaz, this.dataExp.totalExpOut,
       this.dataExp.totalExpRest, this.dataExp.totalExpSau, this.dataExp.totalExpServ, this.dataExp.totalExpSup]
    }
  },
  mounted(){
    this.configShowDonut();
  },
  methods:{
    configShowDonut(){
      var elementExist = document.getElementById("apexDonutExp")

      if(elementExist){
        if(this.dataExp.totalExpHouse != 0){
          document.getElementById("apexDonutExp").classList.add("one")
        } else{
          document.getElementById("apexDonutExp").classList.remove("one")
        }

        if(this.dataExp.totalExpInvestEduc != 0){
          document.getElementById("apexDonutExp").classList.add("two")
        } else{
          document.getElementById("apexDonutExp").classList.remove("two")
        }

        if(this.dataExp.totalExpInvestElet != 0){
          document.getElementById("apexDonutExp").classList.add("three")
        } else{
          document.getElementById("apexDonutExp").classList.remove("three")
        }
        
        if(this.dataExp.totalExpLaz != 0){
          document.getElementById("apexDonutExp").classList.add("four")
        } else{
          document.getElementById("apexDonutExp").classList.remove("four")
        }
        
        if(this.dataExp.totalExpOut != 0){
          document.getElementById("apexDonutExp").classList.add("five")
        } else{
          document.getElementById("apexDonutExp").classList.remove("five")
        }

        if(this.dataExp.totalExpRest != 0){
