    filterData (value, search2) {
      return  value != null &&
        search2 != null &&
        typeof value === 'string' &&
        (value.toString().replace(".", ",").toLowerCase().indexOf(search2.toLowerCase()) !== -1 || 
        value.toString().replace(",", ".").toLowerCase().indexOf(search2.toLowerCase()) !== -1)
    },
    configDataLine(date){
      var jan = 0;
      var fev = 0;
      var mar = 0;
      var abr = 0;
      var may = 0;
      var jun = 0;
      var jul = 0;
      var aug = 0;
      var set = 0;
      var out = 0;
      var nov = 0;
      var dez = 0;

      if(this.allFormsPagt[2].data != null){
        this.allFormsPagt[2].data.forEach(element => {
          var [year, month] = element.date.split('-')
          if(year == date){
            if(month == '01'){
              jan = this.round(jan, element.value)
            } else if(month == '02'){
              fev = this.round(fev, element.value)
            } else if(month == '03'){
              mar = this.round(mar, element.value)
            } else if(month == '04'){
              abr = this.round(abr, element.value)
            } else if(month == '05'){
              may = this.round(may, element.value)
            } else if(month == '06'){
              jun = this.round(jun, element.value)
            } else if(month == '07'){
              jul = this.round(jul, element.value)
            } else if(month == '08'){
              aug = this.round(aug, element.value)
            } else if(month == '09'){
              set = this.round(set, element.value)
            } else if(month == '10'){
              out = this.round(out, element.value)
            } else if(month == '11'){
              nov = this.round(nov, element.value)
            } else if(month == '12'){
              dez = this.round(dez, element.value)
            }
          }
        })
      }

      var newData = [jan, fev, mar, abr, may, jun, jul, aug, set, out, nov, dez]

      this.dataExp.seriesLine = [{
        name: 'Total',
        data: newData
      }]
    },
    createArrayData(){
      var currentYear = (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10).split('-')[0]
      this.yearSelected = parseInt(currentYear)

      var minYear = parseInt(currentYear) - 100;
      var maxYear = parseInt(currentYear) + 100;
      for(var x = minYear; x <= maxYear; x++){
        if(this.arrayYears == []){
          this.arrayYears = x
        } else{
          this.arrayYears.push(x)
        }
      }
    },
    setValuesSeries(){
      Vue.set(this.dataExp.seriesDonut, 0, (this.dataExp.totalExpHouse))
      Vue.set(this.dataExp.seriesDonut, 1, (this.dataExp.totalExpInvestEduc))
      Vue.set(this.dataExp.seriesDonut, 2, (this.dataExp.totalExpInvestElet))
      Vue.set(this.dataExp.seriesDonut, 3, (this.dataExp.totalExpLaz))
      Vue.set(this.dataExp.seriesDonut, 4, (this.dataExp.totalExpOut))
      Vue.set(this.dataExp.seriesDonut, 5, (this.dataExp.totalExpRest))
      Vue.set(this.dataExp.seriesDonut, 6, (this.dataExp.totalExpSau))
      Vue.set(this.dataExp.seriesDonut, 7, (this.dataExp.totalExpServ))
      Vue.set(this.dataExp.seriesDonut, 8, (this.dataExp.totalExpSup))
    },
    filterByDate(){
      if(this.dateFilterInit == '' || this.dateFilterFinal == ''){
        this.dataExp.error = true;
        this.dataExp.msgError = 'Preencha os campos data inicial e final antes de filtrar';
      } else if(this.dateFilterFinal >= this.dateFilterInit){
        this.dataExp.message = ''
        this.configData();
        this.setValuesSeries();
        this.verifyMessage();
       
        this.configShowDonut();
      } else {
        this.dataExp.error = true;
        this.dataExp.msgError = 'A data inicial deve ser menor ou igual a data final';
      }
    },
    clearDateFilter(){
      this.dateFilterInit = ''
      this.dateFilterFinal = ''
      this.dataExp.message = ''
      this.dateFilterFormattedInit = ''
      this.dateFilterFormattedFinal = ''
      this.configData();
      this.setValuesSeries();
      this.configShowDonut();
    },
    newExp(continueSave){
      if(this.dataExp.newExpValue == 'R$ 0,00' ){
        this.dataExp.error = true;
        this.dataExp.msgError = 'No  possivel cadastrar uma despesa com valor zerado';
      } else if(this.dataExp.newExpSelect == ""){
        this.dataExp.error = true;
        this.dataExp.msgError = 'No  possivel cadastrar uma despesa sem categoria';
      } else{
        this.dataExp.message = ''
        this.generateData();

        this.dataExp.newExpValue = 0
        this.dataExp.newExpSelect = ''
        this.dataExp.newExpDesc = ''
        this.dataExp.snackbarNewExp = true;
        this.configData();
        this.configDataLine(this.yearSelected);
        this.configShowDonut();

        this.verifyMessage();
        this.dataExp.msgSuccess = 'Despesa cadastrada com sucesso'
        if(!continueSave){
          this.dialog = false;
        }
      }
