        this.allFormsPagt[2].data[elementIndex].idCategory = 6;
      }
      
      if(this.allFormsPagt[2].data[elementIndex].idCategory == 'Servios') {
        if(desc == 'Casa' || desc == 'Educao' || desc == 'Eletrnicos' || desc == 'Lazer' 
        || desc == 'Outros' || desc == 'Restaurante' || desc == 'Sade' || desc == 'Supermercado' ){
          this.allFormsPagt[2].data[elementIndex].desc = 'Servios'
        }
        this.allFormsPagt[2].data[elementIndex].idCategory = 7;
      }
      
      if(this.allFormsPagt[2].data[elementIndex].idCategory == 'Supermercado') {
        if(desc == 'Casa' || desc == 'Educao' || desc == 'Eletrnicos' || desc == 'Lazer' 
        || desc == 'Outros' || desc == 'Restaurante' || desc == 'Sade' || desc == 'Supermercado' ){
          this.allFormsPagt[2].data[elementIndex].desc = 'Supermercado'
        }
        this.allFormsPagt[2].data[elementIndex].idCategory = 8;
      }
      
    },
    round(num, num2){
      if(num2){
        return (Math.round((num + parseFloat(num2)) * 100) / 100);
      }

      if(!num2){
        return (Math.round(num * 100) / 100);
      }
    },
    convertMoneyFloat(value){
      if(value === ""){
        value =  0;
      }else{
        value = value.replace("R$ ", "")
        value = value.replace(".","");
        value = value.replace(",",".");
        value = parseFloat(value);
      }
      return value;
    },
    toBrl(value){
      return (Math.round(value * 100) / 100).toFixed(2);
    },
