              if(this.desserts2.length == 0) this.desserts2 = [{idExp: element.idExp, desc: element.desc, idCategory: 'Educao', date: element.date, value: this.toBrl(element.value)}]
              else this.desserts2.push({idExp: element.idExp, desc: element.desc, idCategory: 'Educao', date: element.date,value: this.toBrl(element.value)})
            }
