    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    sumValues(){
      var value = this.dataExp.totalExpHouse + this.dataExp.totalExpInvestEduc + this.dataExp.totalExpInvestElet + this.dataExp.totalExpLaz + this.dataExp.totalExpOut
      + this.dataExp.totalExpRest + this.dataExp.totalExpSau + this.dataExp.totalExpServ + this.dataExp.totalExpSup;
      return (Math.round(value * 100) / 100).toFixed(2).replace(".",",");
    }
  },
  filters: {
    toBrl(value){
      return (Math.round(value * 100) / 100).toFixed(2).replace(".",",");
    },
    toDate(value){
      return value.substr(0, 10)
    }, 
    
  },
  computed: {
    computedDateFormatted () {
      return this.formatDate(this.date)
    },
    
  },
  watch: {
    date () {
      this.dateFormatted = this.formatDate(this.date);
    },
    'editedItem.date'(){
      this.editedItem.dateFormatted = this.formatDate(this.editedItem.date);
    },
    dateFilterInit(){
      this.dateFilterFormattedInit = this.formatDate(this.dateFilterInit);
    },
    dateFilterFinal(){
      this.dateFilterFormattedFinal = this.formatDate(this.dateFilterFinal);
    },
    yearSelected(){
      this.configDataLine(this.yearSelected)
    },
  
  },
}
</script>

